import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import jwt_decode from 'jwt-decode';
Vue.use(Vuex)

const _apiBaseURLforuserManagement = process.env.VUE_APP_UmApisBaseUrl+"/User";
const _apiBaseURLforVendor = process.env.VUE_APP_VmApisBaseUrl;
export default new Vuex.Store({
    state: {
        roles:[],
        warehouses:[],
        clientAppToken:null,
        clientAppDecodedToken:null
    },
    mutations: {
        updateSession(state, token){
            Vue.set(state, 'clientAppToken', token);
            Vue.set(state, 'clientAppDecodedToken', jwt_decode(token));
        },
        updateRole(state, arrRoles){
            state.roles=[];
            arrRoles.forEach(element => {
                state.roles.push(element);
            });
        },
        UpdateWarehouses(state,arrWarehouses){
            state.warehouses=[];
            arrWarehouses.forEach(element => {
                state.warehouses.push(element);
            });
        }
    },
    actions: {
        getRolesAsync : async({commit},orgType)=>{
            axios.get(_apiBaseURLforuserManagement+'/GetRoles?orgType='+orgType).then(response=>{commit("updateRole", response.data)});
        },
        getOrgWarehousesAsync: async({commit}, orgId) =>{
            await axios.get(_apiBaseURLforVendor+'/Manufacturer/GetOrganizationWarehouses/'+orgId).then(response =>{commit('UpdateWarehouses',response.data)});
        },
    },
    getters: {
        roles: state=> state.roles,
        warehouses:state=>state.warehouses
    }
})